 * Replace them with your own styles or remove the file completely.
 */

.wp-block-prc-block-timeline {
	--height: 20px;
	color: inherit;
	background: inherit;

	input[type="range"] {
		width: 100%;
		margin: 20px 0;
		-webkit-appearance: none;
		height: var(--height);
		background: #d3d3d3;
		outline: none;
		opacity: 0.7;
		transition: opacity 0.2s;
		&:hover {
			opacity: 1;
		}
		&::-webkit-slider-thumb {
			-webkit-appearance: none;
			appearance: none;
			width: 25px;
			height: 25px;
			background: #4CAF50;
			cursor: pointer;
			border-radius: 50%;
		}
		&::-moz-range-thumb {
			width: 25px;
			height: 25px;
			background: #4CAF50;
			cursor: pointer;
			border-radius: 50%;
		}
	}

	.ticks {
		position: relative;
		height: var(--height);
		display: flex;
		justify-content: space-between;
		align-items: center;
		list-style-type: none;
		padding: 0;
		font-size: 12px;
		// make it so that only the first, last, and middle elements are visible, the rest should still exist and be clickable but have no text in them...
		// .tick:not(:first-child):not(:last-child):not(:nth-child(3n)) {
		// 	visibility: hidden;
		// }
		.tick {
			color: #555;
			span {
				font-size: 0.8em;
				position: absolute;
				top: 15px;
				transform: translateX(-50%);
			}
		}
	}
}


